    top: 0;
    left: 100%;
    width: 10px;
    height: 10px;
    background: url("../img/stack.svg#hero-angle-mobile") center center/contain no-repeat;
  }
}
.hero__btn {
  align-self: start;
}
.hero__background {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
}
.hero__background picture {
  object-fit: contain;
}
.hero__background img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero__pagination .swiper-pagination-bullet:not(.swiper-pagination-bullet-active) {
  background: url("../img/stack.svg#slider-pagination-bullet") center center/contain no-repeat;
  outline: none;
  background-color: unset;
  opacity: 1;
}

.hero__pagination .swiper-pagination-bullet.swiper-pagination-bullet-active {
  background: url("../img/stack.svg#slider-pagination-bullet-active") center center/contain no-repeat;
  outline: none;
  background-color: unset;
  opacity: 1;
}

.hero__pagination .swiper-pagination-bullet:hover {
  background: url("../img/stack.svg#slider-pagination-bullet-hover") center center/contain no-repeat;
  outline: none;
  background-color: unset;
  opacity: 1;
}

.hero__pagination .swiper-pagination-bullet:focus {
  background: url("../img/stack.svg#slider-pagination-bullet-hover") center center/contain no-repeat;
  outline: none;
  background-color: unset;
  opacity: 1;
}

.about__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 60px;
  padding-top: 131px;
}
@media (max-width: 1199px) {
  .about__container {
    grid-template-columns: 100%;
    row-gap: 40px;
    padding-top: 113px;
  }
}
@media (max-width: 767px) {
  .about__container {
    padding-top: 72px;
  }
}
.about__wrapper {
  max-width: 498px;
}
@media (max-width: 1199px) {
  .about__wrapper {
    max-width: 78%;
  }
}
@media (max-width: 767px) {
  .about__wrapper {
    max-width: 98%;
  }
}
.about__wrapper h2 {
  margin: 0;
  padding: 0;
  margin-bottom: 43px;
}
@media (max-width: 767px) {
  .about__wrapper h2 {
    margin-bottom: 21px;
  }
}
.about__wrapper h3 {
  margin: 0;
  padding: 0;
  margin-bottom: 46px;
}
@media (max-width: 1199px) {
  .about__wrapper h3 {
    margin-bottom: 30px;
  }
}
@media (max-width: 767px) {
  .about__wrapper h3 {
    margin-bottom: 20px;
  }
}
.about__wrapper p {
  margin: 0;
  padding: 0;
  margin-bottom: 40px;
}
@media (max-width: 767px) {
  .about__wrapper p {
    margin-bottom: 30px;
  }
}
.about__features {
  grid-column: 1/3;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 52px;
  row-gap: 54px;
  margin: 0;
  padding: 70px 106px 56px 106px;
  border-radius: 20px;
  background: #316dc2;
}
@media (max-width: 1199px) {
  .about__features {
    grid-column: 1/2;
    column-gap: 30px;
    row-gap: 20px;
    margin-top: 20px;
    padding: 40px;
  }
}
@media (max-width: 767px) {
  .about__features {
    grid-template-columns: 100%;
    column-gap: 0;
    row-gap: 0;
    padding: 22px 20px 40px 10px;
  }
}
.about__features dt {
  margin: 0;
  padding: 0;
  max-width: 171px;
  color: #ffffff;
  font-family: "Manrope", "Arial", sans-serif;
  font-size: 26px;
  font-style: normal;
  font-weight: 400;
  line-height: 30px;
  letter-spacing: 0.26px;
  text-transform: uppercase;
}
@media (max-width: 1199px) {
  .about__features dt {
    font-size: 20px;
    line-height: 22px;
    letter-spacing: 0.2px;
    margin-left: 30px;
    max-width: 70%;
  }
}
@media (max-width: 767px) {
  .about__features dt {
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.16px;
    max-width: 85%;
    margin-left: 20px;
  }
}
.about__features dd {
  margin: 0;
  padding: 0;
  color: #f0efe9;
  font-family: "Manrope", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 20px;
  max-width: 190px;
}
@media (max-width: 1199px) {
  .about__features dd {
    margin-left: 30px;
    max-width: 75%;
  }
}
@media (max-width: 767px) {
  .about__features dd {
    font-size: 14px;
    line-height: 20px;
    max-width: 74%;
    margin-left: 20px;
  }
}
.about__features-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 30px 0;
  border-radius: 16px;
  border: 1px solid #f0efe9;
  margin-top: 22px;
  margin-left: 20px;
}
.about__features-wrapper::before {
  content: "";
  display: block;
  position: absolute;
  top: -21px;
  left: -21px;
  width: 42px;
  height: 42px;
  background: url("../img/stack.svg#white-star-blue-round") center center/contain no-repeat;
}
@media (max-width: 1199px) {
  .about__features-wrapper {
    flex-direction: column;
    gap: 12px;
  }
}
