@media (max-width: 1199px) {
  .programs__content h3 {
    font-size: 20px;
    line-height: 24px;
  }
}
@media (max-width: 767px) {
  .programs__content h3 {
    font-size: 16px;
    line-height: 18px;
  }
}
.programs__content p {
  margin: 0;
  padding: 0;
  color: #484848;
  font-family: "Manrope", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}
@media (max-width: 1199px) {
  .programs__content p {
    font-size: 14px;
    line-height: 20px;
